



























import { PostModel } from "@/x-vue/interfaces/forum.interface";
import Vue from "vue";

import { Component, Prop } from "vue-property-decorator";

@Component({})
export default class ThumbnailWithInlineText extends Vue {
  @Prop({
    default: () => {
      return new PostModel().fromJson({
        idx: 1,
        title:
          "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.",
        files: [
          {
            url: "https://symbols.getvecta.com/stencil_82/45_google-icon.d8d982f8a1.svg",
          },
        ],
        relativeUrl: "#",
      });
    },
  })
  post!: PostModel;

  get src(): string {
    if (!this.post.files.length) return "";
    else return this.post.files[0].url;
  }
}
